@use 'global' as *;

.common-fv {
  position: relative;

  background-image: url(../images/campaign/campaign-fv-SP.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
  height: rem(460);

  @include mq('md') {
    background-image: url(../images/campaign/campaign-fv-PC.jpg);
    height: rem(548);
  }
}
// about-us画像
.common-fv.common-fv--about {
  background-image: url(../images/about-us/about-us-SP.jpg);

  @include mq('md') {
    background-image: url(../images/about-us/about-us-PC.jpg);
  }
}
.common-fv.common-fv--information {
  background-image: url(../images/information/information-fv_SP.jpg);

  @include mq('md') {
    background-image: url(../images/information/information-fv_PC.jpg);
  }
}
// information画像



.common-fv__title {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  font-family: $second-font-family;
  font-weight: $regular;
  font-size: rem(40);
  color: $white;
  @include mq('md') {
    font-size: rem(80);
    top: 59.3%;
    line-height: 1.25;
  }
}
